import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { ExperienceWrapper } from '../style'

class Experience extends PureComponent {
  render() {
    return (
      <ExperienceWrapper>
        <div className='tag'>
          <div className='tag-icon'>
            <i className='iconfont'>&#xe71a;</i>
          </div>
          <div className='tag-body'>
            <h2 className='label'>项目与工作经验</h2>
            <p className='label-en'>Experience</p>
          </div>
        </div>
        <div className='line'></div>
        { this.showExperience() }
        <div className='ball'></div>
      </ExperienceWrapper>
    )
  }

  showExperience() {
    return this.props.experience.map((item, index) => {
      return (
        <div className='content' key={index}>
          <div className='time'>
            <div className='time-tag'>
              <i className='iconfont' dangerouslySetInnerHTML={{ __html: item.icon }}></i>
            </div>
            <div className='during'>
              <div>{item.intro}</div>
              <div>{item.time}</div>
            </div>
          </div>
          <div className='body'>
            <div className='body-content'>
              <h2 className='project-name'>{item.projectName}</h2>
              {
                this.props.experience[index].detail.map((item, index) => {
                  return (
                    <div className='info' key={index}>
                      <div className='iconfont icon'>&#xe609;</div>
                      <div className='detail' dangerouslySetInnerHTML={{ __html: item }}></div>
                    </div>
                  )
                })
              }
            </div>
          </div>
        </div>
      )
    })
  }
}

const mapState = state => ({
  experience: state.resume.experience
})

export default connect(mapState, null)(Experience)