import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { EducationWrapper } from '../style'

class Education extends PureComponent {
  render() {
    return (
      <EducationWrapper>
        <div className='tag'>
          <div className='tag-icon'>
            <i className='iconfont'>&#xe77f;</i>
          </div>
          <div className='tag-body'>
            <h2 className='label'>教育经历</h2>
            <p className='label-en'>Education</p>
          </div>
        </div>
        <div className='line'></div>
        <div className='time'>
          <div className='time-tag'>
            <i className='iconfont'>&#xe781;</i>
          </div>
          <div className='during'>2016~2020</div>
        </div>
        <div className='body'>
          <div className='body-content'>
            <p className='university'>{this.props.school}</p>
            <div className='info'>
              <div className='iconfont icon'>&#xe6d0;</div>
              <div className='detail'>软件工程（本科）</div>
            </div>
            {
              this.props.detail.map((item, index) => {
                return (
                  <div className='info' key={index}>
                    <div className='iconfont icon'>&#xe730;</div>
                    <div className='detail' dangerouslySetInnerHTML={{ __html: item }}></div>
                  </div>
                )
              })
            }
          </div>
        </div>
        <div className='ball'></div>
      </EducationWrapper>
    )
  }
}

const mapState = state => ({
  detail: state.resume.education.detail,
  school: state.resume.education.school
})

export default connect(mapState, null)(Education)