import React, { PureComponent } from 'react'
import { HeaderWrapper } from '../style'
import headImg from '../../../static/images/wx-code.png'

class MyHeader extends PureComponent {
  render() {
    return (
      <HeaderWrapper>
        <div className='head'>
          <img className='head-img' src={headImg} alt=""/>
        </div>
        <div className='contact'>
          <p className='contact-info'><i className='iconfont'>&#xe7aa;</i>951045989@qq.com</p>
          <p className='contact-info'><i className='iconfont'>&#xe617;</i>iwm5201314</p>
          <p className='contact-info'><i className='iconfont'>&#xe780;</i>https://github.com/cylhah</p>
          <p className='contact-info'><i className='iconfont'>&#xe665;</i>https://cylhah.github.io/</p>
          <p className='contact-info'><i className='iconfont'>&#xe62c;</i>13758158272</p>
        </div>
        <div className='name'>
          <h1 className='name-myname'>陈永雷</h1>
          <p className='name-job'>前端实习生</p>
        </div>
      </HeaderWrapper>
    )
  }
}

export default MyHeader
