import styled from 'styled-components'

export const ResumeWrapper = styled.div`
  width: 100%;
  height: 100%;
  padding: 20px 0;
  background: rgb(224, 224, 224);
  .main {
    position: relative;
    width: 1030px;
    margin: 0 auto;
    .main-content {
      padding: 30px 45px;
      background: #fff;
    }
  }
`

export const HeaderWrapper = styled.div`
  background: rgb(51,51,51);
  color: #fff;
  display: flex;
  box-sizing: border-box;
  padding: 30px 45px;
  .head {
    width: 130px;
    height: 130px;
    .head-img {
      width: 100%;
      height: 100%;
    }
  }
  .contact {
    margin-left: 20px;
    font-size: 18px;
    width: 60%;
    .contact-info {
      line-height: 26px;
      i {
        font-size: 18px;
        margin-right: 10px;
      }
    }
  }
  .name {
    text-align: right;
    .name-myname {
      font-size: 72px;
      margin-bottom: 15px;
    }
    .name-job {
      font-size: 20px;
    }
  }
`

export const EducationWrapper = styled.div`
  margin-bottom: 15px;
  .tag {
    display: flex;
    .tag-icon {
      width: 44px;
      height: 44px;
      background: rgb(51,51,51);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      margin-right: 10px;
      i {
        font-size: 26px;
      }
    }
    .tag-body {
      .label {
        font-size: 20px;
        font-weight: bold;
        margin: 3px 0;
      }
      .label-en {
        color: #008080;
        font-weight: bold;
      }
    }
  }
  .line {
    width: 20px;
    height: 10px;
    border-right: 4px solid rgb(51,51,51);
  }
  .time {
    position: relative;
    padding-left: 3px;
    display: flex;
    align-items: center;
    .time-tag {
      width: 38px;
      flex-shrink: 0;
      height: 38px;
      background: #008080;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 10;
      i {
        color: #fff;
        font-size: 20px;
      }
    }
    .during {
      background: rgb(51,51,51);
      color: #fff;
      height: 28px;
      line-height: 28px;
      padding-left: 20px;
      border-radius: 5px;
      width: 100%;
      margin-left: -10px;
    }
  }
  .body {
    padding-left: 20px;
    .body-content {
      padding: 10px 0 10px 20px;
      border-left: 4px solid rgb(51,51,51);
      .university {
        font-size: 32px;
        font-weight: bold;
        margin-bottom: 16px;
      }
      .info {
        display: flex;
        line-height: 24px;
        .icon {
          font-size: 24px;
          margin-right: 10px;
        }
        .detail {
          span {
            font-weight: bold;
            color: #008080;
          }
        }
      }
    }
  }
  .ball {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: rgb(51,51,51);
    margin-left: 15px;
  }
`

