import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { ExperienceWrapper } from '../style'

class Skills extends PureComponent {
  render() {
    return (
      <ExperienceWrapper>
        <div className='tag'>
          <div className='tag-icon'>
            <i className='iconfont'>&#xe6c1;</i>
          </div>
          <div className='tag-body'>
            <h2 className='label'>技能</h2>
            <p className='label-en'>Skills</p>
          </div>
        </div>
        <div className='line'></div>
        { this.showSkills() }
        <div className='ball'></div>
      </ExperienceWrapper>
    )
  }

  showSkills() {
    return this.props.skills.map((item, index) => {
      return (
        <div className='content' key={index}>
          <div className='time'>
            <div className='time-tag'>
              <i className='iconfont' dangerouslySetInnerHTML={{ __html: item.icon }}></i>
            </div>
            <div className='during'>
              <div>{item.type}</div>
            </div>
          </div>
          <div className='body'>
            <div className='body-content'>
              {
                item.items.map((item, index) => {
                  let details = item.detail.map((item, index) => {
                    return (
                      <div className='info' key={index}>
                        <div className='iconfont icon'>&#xe612;</div>
                        <div className='detail' dangerouslySetInnerHTML={{ __html: item }}></div>
                      </div>
                    )
                  })
                  return (
                    <div className='skill' key={index}>
                      <h2 className='project-name' dangerouslySetInnerHTML={{ __html: item.skillName }}></h2>
                      {details}
                    </div>
                  )
                })
              }
            </div>
          </div>
        </div>
      )
    })
  }
}

const mapState = state => ({
  skills: state.resume.skills
})

export default connect(mapState, null)(Skills)